const baseUrl = process.env.REACT_APP_API_BASE_URL;

const addproperty = async (body) => {
  try {
    const response = await axios({
      url: `${baseUrl}/admin/property/create`,
      method: "POST",
      data: body,
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    return response;
  } catch (error) {
    throw error;
  }
};

